'use client'
import React, { useState } from 'react'; 
import {
  MdSpaceDashboard, MdPeople, MdEvent, MdAttachMoney, MdGroups, MdVolunteerActivism,
  MdMessage, MdPermMedia, MdSettings, MdPerson, MdChevronLeft, MdChevronRight
} from "react-icons/md";

const NAV_ITEMS = [
  { label: "Dashboard", icon: <MdSpaceDashboard />, view: "dashboard" },
  { label: "Profile", icon: <MdPerson />, view: "profile" },
  { label: "Members", icon: <MdPeople />, view: "members" },
  // { label: "Attendance", icon: <MdEvent />, view: "attendance" },
  { label: "Events", icon: <MdEvent />, view: "events" },
  { label: "Donations", icon: <MdAttachMoney />, view: "donations" },
  { label: "Groups", icon: <MdGroups />, view: "groups" },
  // { label: "Volunteers", icon: <MdVolunteerActivism />, view: "volunteers" },
  { label: "Communication", icon: <MdMessage />, view: "communication" },
  { label: "Media", icon: <MdPermMedia />, view: "media" },
  { label: "Settings", icon: <MdSettings />, view: "settings" },
];

const SideBarDash = ({ onMenuClick }) => {
  const [isExpanded, setIsExpanded] = useState(true);

  return (
    <div className={`flex h-auto ${isExpanded ? 'w-full md:w-64 lg:w-72' : 'w-20'} bg-white pt-4  rounded-lg m-1 transition-all duration-600`}>
      <div className=''>
        {/* Toggle Button */}
        <div className='flex justify-end'>
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className=' bg-persian-blue-600 hover:bg-persian-blue-700 text-white p-1 rounded-lg transition-all duration-600'
            title={isExpanded ? 'Collapse menu' : 'Expand menu'}
          >
            {isExpanded ? <MdChevronLeft size={20} /> : <MdChevronRight size={20} />}
          </button>
        </div>

        {isExpanded && (
          <>
            <h1 className='flex justify-center text-xl md:text-2xl font-semibold text-persian-blue-700 '>Main Menu</h1>
            <ul className="flex flex-col justify-center p-2 md:p-2 w-full">
              {NAV_ITEMS.map(({ label, icon, view }) => (
                <li
                  key={label}
                  onClick={() => onMenuClick(view)}
                  className="
                    transition-all duration-400  
                    text-persian-blue-600 group hover:bg-persian-blue-600 hover:text-persian-blue-50
                    hover:rounded-lg cursor-pointer flex pl-3 md:pl-5 items-center text-base md:text-lg lg:text-xl gap-2 md:gap-3 p-4 md:p-4...
                  "
                >
                  <span className="group-hover:scale-110 transition-all duration-400">{icon}</span>
                  <span className="group-hover:scale-105 transition-all duration-400">{label}</span>
                </li>
              ))}
            </ul>
          </>
        )}

        {/* Collapsed view - show icons only */}
        {!isExpanded && (
          <ul className="flex flex-col justify-center p-2 w-full gap-6 mt-11">
            {NAV_ITEMS.map(({ label, icon, view }) => (
              <li
                key={label}
                onClick={() => onMenuClick(view)}
                className="
                  transition-all duration-400  
                  text-persian-blue-600 group  hover:bg-persian-blue-600 hover:text-persian-blue-50
                  hover:rounded-lg cursor-pointer flex justify-center items-center text-xl p-2
                "
                title={label}
              >
                <span className="group-hover:scale-110 transition-all duration-500">{icon}</span>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default SideBarDash;
